// Navigation Bar

nav {
	padding: 20px;
	position: fixed;
	width: 100%;
	background-color: #FFF;
	box-shadow: 0 5px 10px rgba(0,0,0,0.05);
	.logo {
		display: inline-block;
		position: relative;
		left: 0;
		font-weight: bold; }
	ul.menu {
		display: inline-block;
		list-style-type: none;
		margin: 0;
		padding: 0;
		float: right;
		li {
			display: inline-block;
			margin-left: 1.2em;
			&:first-of-type {
				margin-left: 0; } } }
	a:hover {
		opacity: 0.6; } }
